.project-heading {
    text-align: center;
    padding: 4rem 0 2rem 0;
}

.project-container {
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat( 3, 1fr );
    gap: 40px;
    padding: 5rem;
}

.project-card {
    background: #1a1919;
    padding: 1.2rem 1rem;
    width: 300px;
    height: 70vh;
}

.project-card img {
    width: 100%;
    height: 35%;
}

.project-title {
    color: #fff;
    padding: 1rem 0;
    text-align: center;
}

.pro-details p {
    padding-bottom: 1rem;
    font-size: 1.1rem;
    text-align: justify;
}

.pro-btns {
    text-align: center;
    margin-top: 10px;
}

.pro-btns .btn {
    padding: 0.5rem 1rem;
    margin-left:10px;
}

@media screen and (max-width: 740px) {
    .project-container {
        max-width: 90%;
        margin: auto;

        grid-template-columns: 1fr;
    }
}